import React, { Component } from 'react'
import { connect } from 'react-redux'
import axios from 'axios'
import { Redirect } from 'react-router-dom'

class TermosPrivacidade extends Component {

    handleClick = () => {
        this.props.history.push("/signup");
    }

    render() {
        const { auth } = this.props;
        return (
            <div className="white lighten-1 z-depth-0">
                <h4 className="grey-text text-darken-3">Termos da politica de privacidade</h4>
                <p>
                    A MyOwnCutlery compromete-se a proteger a sua privacidade e a tornar a sua experincia de compra o mais agradvel possvel. Esta declarao  aplicvel apenas ao site da MyOwnCutlery, regulando a recolha e tratamentos dos dados pessoais dos nossos clientes. Ao aceder e utilizar o website endereo url do site, o Utilizador est a consentir a aceitao e respeito pelas condies descritas na presente declarao.</p>

                <h5 className="grey-text text-darken-3">Recolha dos seus dados pessoais</h5>
                <p>Os seus dados so recolhidos por duas razes:</p>
                <p>1 - Processamento da sua encomenda</p>
                <p>2 - Fornecimento de um melhor servio</p>

                <h5 className="grey-text text-darken-3">Dados pessoais recolhidos pela MyOwnCutlery:</h5>
                <p>1  Endereo de e-mail;</p>
                <p>2  Nome;</p>
                <p>3  Morada: Casa ou trabalho;</p>
                <p>4  NIF</p>
                <p>6  Data de Nascimento.</p>

                <p>Por favor note que ao revelar dados pessoais em meios pblicos da MyOwnCutlery como: Facebook, esta informao poder ser vista e utilizada por terceiros.</p>

                <h5 className="grey-text text-darken-3">Uso dos seus dados pessoais</h5>
                <p>A MyOwnCutlery recolhe e utiliza os seus dados pessoais para gerir o site endereo url do site e para lhe prestar servios que lhe foram requisitados. Os seus dados pessoais so tambm utilizados para o informar de novos produtos e/ou servios disponveis na nossa pgina. Poder ser ainda contactado no mbito de questionrios e pesquisas, para conhecer a sua opinio sobre os servios prestados ou sobre novos servios que poderemos vir a oferecer.</p>

                <p>A MyOwnCutlery no vende ou disponibiliza os dados dos seus clientes a terceiros. Poder, no entanto, ser contactado em nome de parceiros de negcio sobre algumas ofertas que podero ser do seu interesse. Nestes casos, os dados pessoais que o identificam (nome, e-mail, morada) no sero transferidos a terceiros mas partilhados com parceiros que nos ajudaro a desenvolver anlises estatsticas (atravs de e-mail ou carta), servio ao cliente ou entregas/recolhas. Todos estes parceiros intermedirios esto proibidos de usar os seus dados para fins distintos dos enumerados acima, encontrando-se ainda obrigados a manter a confidencialidade dos mesmos.</p>


                <p>A MyOwnCutlery no usa ou revela informaes pessoais sem o seu explcito consentimento.</p>
                <p>A MyOwnCutlery s fornece a sua informao, sem o seu consentimento, se requerido por lei ou se de boa-f acreditar que esta ao  necessria:</p>
                <p>(a) Conforme alguns decretos de lei ou notificaes jurdicas sobre a MyOwnCutlery ou o site;</p>
                <p>(b) Proteger e defender direitos da MyOwnCutlery;</p>
                <p>(c) Agir sobre circunstncias especiais para proteger os utilizadores de endereo url do site ou o pblico em geral.</p>

                <h5 className="grey-text text-darken-3">Segurana dos seus dados pessoais</h5>
                <p>A MyOwnCutlery guarda as informaes pessoais que nos fornece em servidores que se encontram em ambiente seguro, protegidos de acessos no autorizados, uso ou divulgao.</p>

                <h5 className="grey-text text-darken-3">Alterao ao documento</h5>
                <p>A MyOwnCutlery poder ocasionalmente alterar esta declarao e a sua Poltica de Privacidade para poder refletir a imagem da empresa e o feedback dos clientes. Incentivamos os nossos clientes a reverem periodicamente a nossa Poltica de Privacidade, para estarem atualizados acerca de como a MyOwnCutlery protege a sua informao.</p>

                <h5 className="grey-text text-darken-3">Contactos</h5>
                <p>A MyOwnCutlery agradece os seus comentrios em relao a esta Poltica de Privacidade. Se acredita que a MyOwnCutlery no cumpre com esta declarao, por favor contacte-nos (reipig@reipig.com). Iremos tentar avaliar a sua sugesto e responder-lhe o mais rapidamente possvel.</p>
                <div className="right" >
                    <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick}>Voltar</button>
                </div>
            </div>
        )
    }
}

export default connect()(TermosPrivacidade)